.modal {
  width: 100vw;
  height: 100vh;
  background-color:rgba($dark-grey, .5);
  backdrop-filter: blur(1px);
  position: fixed;
  top: 0;
  left:0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.modal-backdrop {
  position: relative;
  width: 100%;
  height: 100%;
}

.modal-image-container{
 width: auto;
 height: 80%;
 position: fixed;
 margin: 20px;
}

.modal-image{
  width: 100%;
  height: 100%;
  object-fit: contain;
  border-radius: 10px;
}






